


.component {
    width: 20%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #dedede;
    border-radius: 10%;
}
.component:hover {
    box-shadow: 2vh 2vh 2vh 2vh rgba(0, 0, 0, 0.5);
}

.component:active {
    box-shadow: 5rem 5rem 5rem 5rem rgba(52, 152, 170, 0.5);
}
.component img {
    width: 50%;
}

.box,
.box1 {
    display: flex;
    flex-direction: column;
    
    
}
.box,
.box1:hover {
    transform-style: inherit;
}



