.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 5rem auto;
    width: 80%;
    
}

button {
    width: 8rem;
    height: 2rem;
    background-color: rgb(203, 210, 13);
    color: #fff;
    border: 1px solid rgb(203, 210, 13);
    border-radius: 12%;
}

.box1,
.box2 {
    width: 80%;
    margin:  auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.red {
    color: red;
    width: 100%;
    text-align: center;
}
.green {
    color: green;
    width: 100%;
    text-align: center;

}